/**********************************
SP mode(初期スタイル)
**********************************/

/* 変数 */
:root {
  --black: #333;
  --dark-gray: #999;
  --gray: #ddd;
  --light-gray: #f6f6f6;
  --white: #fff;

  --light-orange: #fae7e1;
  --orange: #ffb3a0;
  --dark-orange: #ff4000;

  --header-height: 5dvh;
  --header-margin-top-bottom-pc: 1.5rem;
  --group-tabs-height: 2rem;
}

* {
  padding: 0;
  margin: 0;
  /* タップ時ハイライトを消す */
  -webkit-tap-highlight-color: transparent;
  box-sizing: border-box;
  &::-webkit-scrollbar {
    background-color: var(--light-gray);
    width: 0.5rem;
    height: 0.5rem;
  }

  &::-webkit-scrollbar-thumb {
    background-color: var(--gray);
    border-radius: 0.5rem;
  }

  &::-webkit-scrollbar-thumb:hover {
    background-color: var(--dark-gray);
  }
}

html {
  /* 1rem */
  font-size: 15px;
  color: var(--black);
  background-color: var(--light-gray);
  height: 100dvh;
}

.header {
  margin: 0.5rem 1rem;
  height: var(--header-height);
  display: flex;
  justify-content: space-between;
  align-items: center;

  .header__left {
    display: flex;
    align-items: center;
    gap: 1rem;

    .header__title {
      font-size: 1.4rem;
      font-weight: bold;
      a {
        text-decoration: none;
        color: var(--black);
      }
    }
  }

  .header__right {
    margin-top: 0.2rem;
    .header__icons {
      display: flex;
      justify-content: right;
      align-items: center;
      cursor: pointer;
      gap: 0.3rem;

      .header__icon {
        opacity: 0.5;
        &:hover {
          opacity: 1;
        }
      }

      .header__icon--active {
        opacity: 1;
      }
    }
  }
}

.main {
  display: flex;
  justify-content: space-between;
  margin: 0 0.5rem;
  height: calc(100dvh - var(--header-height) - 2rem);
}

/********************************************
種目ボタン
********************************************/
.event-dropdown {
  position: relative;
  width: fit-content;
  width: 7rem;
}

.event-dropdown__selected {
  padding: 0.2rem;
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 0.1rem;
  border-radius: 1rem;
  background-color: var(--black);
  cursor: pointer;
  color: var(--white);
  user-select: none;

  .event-dropdown__text {
    width: 4rem;
    text-align: center;
  }

  .event-dropdown__icon {
    opacity: 0.8;
    display: flex;
    justify-content: center;
    align-items: center;
    transition: transform 0.3s ease; /* スムーズな回転 */
  }

  .event-dropdown__icon--open {
    transform: rotate(180deg);
  }
}

.event-dropdown__menu {
  position: absolute;
  top: 100%;
  left: 0;
  width: 100%;
  background-color: var(--light-gray);
  border-radius: 1rem;
  box-shadow: 0 0 0.2rem 0.05rem var(--gray);
  z-index: 1000;
}

.event-dropdown__item {
  padding: 0.5rem;
  cursor: pointer;
  text-align: center;
  font-size: 1rem;
  color: var(--black);
}

.event-dropdown__item--active {
  background-color: var(--black);
  color: var(--white);
}

/********************************************
要求グループタブ 
********************************************/
.group-tabs {
  background-color: var(--white);
  display: flex;
  gap: 0.5rem;
  overflow-x: scroll;
  &::-webkit-scrollbar {
    display: none;
  }
  .group-tabs__item {
    cursor: pointer;
    padding: 0.5rem;
    white-space: nowrap;
    user-select: none;
  }
  .group-tabs__item:hover:not(.group-tabs__item--active) {
    color: var(--dark-gray);
  }
  .group-tabs__item--active {
    transition: all 0.2s;
    border-bottom: 0.1rem solid var(--dark-gray);
    font-weight: bold;
  }
}

/********************************************
難度表
********************************************/
.elements {
  padding: 0.5rem 1rem;
  background-color: var(--white);
  border-radius: 1rem;
  box-shadow: 0 0.2rem 0.5rem 0.1rem var(--gray);
  .elements__header {
    height: var(--group-tabs-height);
    position: sticky;
    top: 0;
  }
  .elements__group {
    padding: 1.5rem 0;
    height: calc(100% - var(--group-tabs-height));
    overflow: scroll;

    .elements__row {
      display: flex;
      flex-wrap: nowrap;

      .elements__tile {
        min-width: 12rem;
        max-width: 12rem;
        min-height: 5rem;
        padding: 1rem;
        margin: 0.2rem;
        border-radius: 0.5rem;
        background-color: var(--white);
        box-shadow: 0 0 0.2rem 0.05rem var(--gray);
        display: flex;
        flex-direction: column;
        gap: 0.2rem;
        user-select: none;

        .elements__labels {
          display: flex;
          gap: 0.5rem;
          font-size: 0.8rem;

          .elements__difficulty {
            background-color: var(--gray);
            padding: 0.2rem;
            width: 1.5rem;
            height: 1.5rem;
            display: flex;
            justify-content: center;
            align-items: center;
            border-radius: 50%;
          }

          .elements__status {
            padding: 0 0.4rem;
            height: 1.5rem;
            display: flex;
            align-items: center;
            display: flex;
            justify-content: center;
            align-items: center;
            border-radius: 0.5rem;
            background-color: var(--gray);
          }
        }

        .elements__alias {
          font-weight: bold;
          padding: 0.2rem;
        }
        .elements__name {
          font-size: 0.9rem;
        }
      }

      /* 選択可能状態 */
      .elements__tile--active {
        &:hover {
          cursor: pointer;
          background-color: var(--light-gray);
        }
      }

      /* 選択済状態 */
      .elements__tile--selected {
        background-color: var(--light-orange);

        &:hover {
          cursor: pointer;
          opacity: 0.8;
        }

        .elements__labels {
          .elements__difficulty {
            background-color: var(--orange);
          }

          .elements__status {
            background-color: var(--orange);
          }
        }
      }

      /* 同一枠選択済状態 */
      .elements__tile--same-slot-selected {
        opacity: 0.6;
      }

      /* 技数制限_グループ状態 */
      .elements__tile--group-limit {
        opacity: 0.6;
      }

      /* 技数制限_全体状態 */
      .elements__tile--total-limit {
        opacity: 0.6;
      }
    }
  }
}

.elements--full {
  width: 100%;
}
.elements--side {
  width: calc(50%);
}

.elements--disabled {
  display: none;
}

/********************************************
演技構成表
********************************************/
.routine {
  padding: 1rem 0.5rem 1rem 1rem;
  background-color: var(--white);
  border-radius: 1rem;
  box-shadow: 0 0.2rem 0.5rem 0.1rem var(--gray);

  .routine__header {
    height: var(--group-tabs-height);
    position: sticky;
    top: 0;
    font-weight: bold;
  }

  .routine__elements {
    height: calc(85dvh - var(--header-height) - var(--group-tabs-height));
    /* overflow: scroll;
    &::-webkit-scrollbar {
      display: none;
    } */
    display: flex;
    flex-direction: column;
    gap: 0.5rem;

    .routine__element--header {
      font-weight: bold;
      border-bottom: 0.1rem solid var(--dark-gray);
    }

    .routine__element {
      padding: 0.2rem;
      display: grid;
      /* No. Connection, Name, EG, 難度, CV, 削除 */
      grid-template-columns: 1rem 1rem 1fr 3rem 2rem 2rem 1rem;
      column-gap: 0.3rem;

      .routine__item {
        display: flex;
        justify-content: left;
        align-items: center;
        line-height: 1.4rem;
      }

      .routine__icon {
        display: flex;
        justify-content: center;
        align-items: center;
        opacity: 0.2;
        &:hover {
          cursor: pointer;
          opacity: 1;
        }
      }

      .routine__icon--active {
        opacity: 1;
      }
    }

    .routine__element--footer {
      font-weight: bold;
      border-top: 0.1rem solid var(--dark-gray);
    }
  }
}

.routine--full {
  width: 100%;
}
.routine--disabled {
  display: none;
}
.routine--side {
  width: calc(50% - 1rem);
}

/**********************************
PC mode
**********************************/
@media (min-width: 700px) {
  .header {
    margin: var(--header-margin-top-bottom-pc) 3rem;
    .header__title {
      font-size: 1.4rem;
    }
  }

  .main {
    margin: 0 1.5rem;
    height: calc(
      100dvh - var(--header-height) - var(--header-margin-top-bottom-pc) - 3rem
    );
  }
  /********************************************
  種目ボタン
  ********************************************/
  .event-buttons {
    display: flex;
    background-color: var(--gray);
    border-radius: 1rem;

    .event-buttons__item {
      cursor: pointer;
      padding: 0.4rem;
      width: 8.5vw;
      text-align: center;
      user-select: none;

      &:hover:not(.event-buttons__item--active) {
        color: var(--dark-gray);
      }
    }

    .event-buttons__item--active {
      border-radius: 1.5rem;
      transition: color 0.5s, background-color 0.5s;
      color: var(--white);
      background-color: var(--black);
    }
  }
  /********************************************
  難度表
  ********************************************/
  .elements {
    padding: 1rem 1.5rem;
  }
  /********************************************
  演技構成表
  ********************************************/
  .routine {
    padding: 1.5rem;
  }
}

/**********************************
hover none mode
(スマホやタブレットでhoverエフェクトが残る症状に対応するため)
**********************************/
@media (hover: none) {
  .elements {
    .elements__group {
      .elements__row {
        .elements__tile--active {
          &:hover {
            background-color: var(--white);
          }
        }

        /* 選択済状態 */
        .elements__tile--selected {
          &:hover {
            cursor: pointer;
            opacity: 1;
          }
        }
      }
    }
  }
}
